import { Link } from "react-router-dom";
import React, { useState } from "react";
import Logo from "../assets/mudransh-img/logo.jpg";
// reactstrap components
import {
  Nav,
  Navbar,
  NavItem,
  NavLink,
  Collapse,
  Container,
  NavbarBrand,
} from "reactstrap";

const Example = (props) => {
  const [collapseOpen, setCollapseOpen] = useState(false);

  const toggle = () => setCollapseOpen(!collapseOpen);

  return (
    <Navbar className="bg-danger fixed-top" expand="lg">
      <Container>
        <NavbarBrand href="#pablo" onClick={(e) => e.preventDefault()}>
          <img src={Logo} height="40px" />
        </NavbarBrand>
        <button
          onClick={() => {
            document.documentElement.classList.toggle("nav-open");
            setCollapseOpen(!collapseOpen);
          }}
          aria-expanded={collapseOpen}
          className="navbar-toggler"
          type="button"
        >
          <span className="navbar-toggler-bar bar1" />
          <span className="navbar-toggler-bar bar2" />
          <span className="navbar-toggler-bar bar3" />
        </button>
        <Collapse isOpen={collapseOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <Link to="/" className="font-weight-bold nav-link">
                Home
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/blogs" className="font-weight-bold nav-link">
                Blogs
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/our-services" className="font-weight-bold nav-link">
                Our Services
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/about-us" className="font-weight-bold nav-link">
                About Us
              </Link>
            </NavItem>
            <NavItem>
              <Link to="/contact-us" className="font-weight-bold nav-link">
                Contact Us
              </Link>
            </NavItem>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  );
};

export default Example;
